body {
  background-color: rgb(5, 5, 5);
  color: rgb(255, 255, 255);
  font-family: 'Muli', sans-serif;
}

.container {
  margin-top: 30px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.cardContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.image{
  width: 500px;
}

.infected {
  margin: 0 2% !important;
  width: 550px !important;
  border-bottom: 10px solid rgb(0, 0, 255);
}

.recovered{
  margin: 0 2% !important;
  width: 550px !important;
  border-bottom: 10px solid rgb(0, 255, 0);
}

.deaths{
  margin: 0 2% !important;
  width: 550px !important;
  border-bottom: 10px solid rgb(255, 0, 0);
}
  
.formControl {
  width: 60% !important;
  margin-top: 50px !important;
  margin-bottom: 20px !important;
  margin-left: 65px !important;
  background-color:rgba(255, 255, 255, 0.705);
}

.barchart {
  display: flex;
  justify-content: center;
  width: 75%;
}
.linechart {
  display: flex;
  justify-content: center;
  width: 75%;
  margin-bottom: 30px;
}
h6{
  color: rgba(255, 255, 255, 0.644);
}


@media only screen and (max-width: 770px) {
  .container {
    margin-top:10px;
  }

  .image {
    width: 100%;
  }

  .infected {
    margin: 4%!important;
    width: 100% !important;
    border-bottom: 10px solid rgb(0, 0, 255);
  }
  
  .recovered{
    margin: 4% !important;
    width: 100% !important;
    border-bottom: 10px solid rgb(0, 255, 0);
  }
  
  .deaths{
    margin: 4%!important;
    width: 100% !important;
    border-bottom: 10px solid rgb(255, 0, 0);
  }

  .barchart {
    width: 100%;

  }
  .linechart {
    width: 100%;

  }
  h6{
    font-size: small;
  }
}





